import Button from "../button/Button";
import Circle from "../circle/Circle";
import "../section/section.scss"
import VerifiedSharpIcon from '@mui/icons-material/VerifiedSharp';
// import { useState, useEffect } from 'react';
import StatisticBar from "../statisticBar/StatisticBar";

function Section({ wider }) {


    return (
        <section>
            <div className="section">
                <Circle dimension={wider ? 168 : 90} wider={wider} />
                <div className="globalDiv">
                    <div className="nameAndButton">
                        <div className="nameDiv">
                            <h2>Marc Ben Bouik</h2>
                            <VerifiedSharpIcon color="primary" />
                            <svg className="point">
                                <circle cx="6" cy="12" r="1.5"></circle>
                                <circle cx="12" cy="12" r="1.5"></circle>
                                <circle cx="18" cy="12" r="1.5"></circle>
                            </svg>
                        </div>
                        <div className="buttonDiv">
                            <Button buttonText="Linkdin" />
                            <Button buttonText="Malt" />
                            <Button buttonText="?" />
                        </div>
                    </div>
                    {wider ? <StatisticBar /> : null}
                    <div className="textDiv">
                        <h3>Dveloppeur junior</h3>
                        <p>Developeur web front-end <br />Intgrateur web <br /> Passion et motiv <br />Mon numro de tlphone <br />Mon Email</p>
                        <p className="diplome">Futur diplom de l'cole <strong>Openclassrooms</strong></p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Section;